body {
    background-color: rgb(250, 250, 250);
}

.container{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

h1{
    color: #044d4db3;
    font-weight: bolder;
}

.image {
    width: 370px;
    margin-top: 50px;

}

@media (max-width: 768px){
    .container {
        margin: 0 10%;

    }

    .image {
        width: 100%;
    }
}